.foot{
  width: 100%;
  height: 90px;
  color: #ccc;
  background-color: rgba(0, 0, 100, 0.8);
  display: flow-root;
  margin: 0;
  padding: 0;
  position: fixed;
  bottom: 0px;
  right: 0px;
}

.crear {
  font-size: 24px;
  color: #ccc;
  margin-left: 20px;
  margin-bottom: 10px;

}

.CreateTodoButton {
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    
  
    transform: rotate(0);
    transition: .3s ease;
    z-index: 1;
  }
  
  .CreateTodoButton:hover {
    transform: rotate(224deg);
  }